.movie {
    border: solid 3px rgba(255, 253, 253, 0.5);
    margin: 4px;
    padding: 4px;
    width: fit-content;
    white-space: nowrap;
}
.movieContainer {
    display: flex;
    flex-direction: row;
    align-items: center;
}
.movie:hover {
    background-color: rgba(47, 255, 227, 0.315);
    text-shadow: 2px 2px rgba(0, 0, 0, 0.349);
}

.pageTitle {
    font-size: 24px;
    text-align:center;
    font-weight: bold;
}

body {
    background-color: rgb(38, 53, 104);
    color:aliceblue;
    margin: 10px;
}
